import { ArrowDownCircleIcon, ChevronDownIcon } from "@heroicons/react/24/outline";
import faq from "../res/faqIcon.svg";
import { useState } from "react";
import json from '../res/data.json'
const items = json.faqs
function FAQs() {
  const [selectedFAQ, setSelectedFAQ] = useState(-1);
  const handleFAQClick = (e, index) => {
    if(selectedFAQ === index)
    setSelectedFAQ(-1);
    else{
      setSelectedFAQ(index);
    }
  }

  console.log(items.length)
  return (
    <div className="h-screen bg-white flex flex-nowrap justify-center">
      <div className="flex flex-col container mb-10 items-center mt-16">
        <div id="title" className="self-center w-fit mb-16">
          <h1 className="text-slate-900 border-t-8 pb-1 border-b-8 rounded-lg border-slate-800 w-full flex justify-center font-bold text-2xl sm:text-4xl md:text-5xl lg:text-6xl">
            Frequently Asked Questions
          </h1>
          <div className="flex mt-16 justify-center">
            <img
              src={faq}
              alt="faqIcon"
              className="flex justify-center max-w-[10rem]"
            ></img>
          </div>
        </div>
        <div id="accordion" className="divide-y-2 mb-10 w-[80vw] sm:w-[70vw] divide-gray-500" >
            {
                (items.map((item, index)=>(
                    <div key={index} className={` bg-slate-900 ${index === 0 ? 'rounded-t-md' : (index===items.length - 1 ? 'rounded-b-md' : '')}`}>
                        <div onClick={(e)=>handleFAQClick(e, index)} className={`flex p-2 lg:p-4 justify-between items-center text-xl `}>
                            <h2 className="text-lg sm:text-xl md:text-2xl tracking-wide text-white basis-6/7 mx-2">{item.title}</h2>
                            <ChevronDownIcon  className={`self-center transition-transform duration-500 w-6 h-6 md:w-8 md:h-8 lg:w-10 lg:h-10 ${index === selectedFAQ ? 'rotate-180 text-white' : ' hover:text-white'}`}></ChevronDownIcon>
                        </div>
                       <div className={`bg-white border-x border-b border-black text-justify motion-safe:transition-dropdown duration-1000 ease-in-out text-slate-900 mt-2 w-full text-lg sm:text-xl md:text-2xl ${index ===selectedFAQ ? 'p-4 max-h-[100vh] overflow-auto' : 'px-4 max-h-0 overflow-hidden border-hidden'} ${index ===items.length - 1 ? 'mb-16' : ''}`}>
                        {item.description}
                        </div>
                    </div>
                )))
            }
        </div>
      </div>
    </div>
  );
}

export default FAQs;
